<template>
  <div :class="theme">
    <Header :widgetData="widgetData" class="visitor-btns" :domain_id="domain_id" @visitor_click="showCallNow" from="visitor" />

    <div v-if="visitor_select != 'call_back' && status != 'connected'" class="height-288 d-flex justify-content-center p-3">
      <div class="p-2 font-size-13px">{{ widgetData.description }}</div>
    </div>
    <CallBack :call_flow_id="flow_id" :api_key="api_key" :domain_id="domain_id" v-if="visitor_select == 'call_back'" />
    <div v-if="visitor_select == 'call_now'" :class="status == 'connected' ? 'callnow-keypad' : ''">
      <div v-if="status == 'connected'" class="width-400">
        <b-card class="h-100 bg-dailer rounded-20 mb-0" body-class="dailer-padding">
          <div v-if="$route.params.from != 'visitor'" class="d-flex justify-content-center">
            <img src="https://infinity.500apps.com/avatars/avatar-47.png" class="avatar rounded-circle mb-3" />
          </div>
          <Timer class="timer-alignment" :domain_id="domain_id" :connection="connection" />
          <KeyPad :domain_id="domain_id" @digitPressed="digitPressed" />
        </b-card>
      </div>
    </div>
    <div class="text-center d-flex justify-content-between">
      <b-button
        :disabled="!isTwilioReady"
        block
        :style="{ background: buttonName == 'Call Now' ? widgetData.btncolor : widgetData.btncolor2, borderColor: buttonName == 'Call Now' ? widgetData.btncolor : widgetData.btncolor2 }"
        :variant="variant"
        class="rounded-top-0"
        @click="prefer"
        v-if="visitor_select == 'call_now'"
        ><span class="fe fe-phone small mr-2"></span>{{ buttonName }}
        <b-spinner small v-if="!isTwilioReady" variant="light" label="Spinning"></b-spinner>
      </b-button>
    </div>
    <div class="pbx-widget-footer">
      <p class="font-size-10px d-flex align-items-center justify-content-center mb-0">
        Powered by <a rel="nofollow" class="ml-2 text-primary cursor-pointer" onclick="window.open('http://www.pbxplus.com/', '_blank')">PBXPlus</a>
      </p>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";
  import Header from "./Header.vue";
  import KeyPad from "./KeyPad.vue";
  import Timer from "./Timer.vue";
  import Call from "../../js/dialer/call";
  import CallLog from "../../js/dialer/call-log";
  import CallBack from "./Call_Back.vue";
  export default {
    components: {
      CallBack,
      Header,
      KeyPad,
      Timer,
    },
    props: {
      flow_id: String,
      api_key: String,
      domain_id: String,
    },
    data() {
      return {
        theme: "visitor preview-keypad",
        is_call_now: false,
        status: "",
        connectedCall: false,
        visitor_select: "",
        connection: "",
        buttonName: "Call Now",
        variant: "success",
        widgetData: {},
        showText: false,
      };
    },
    computed: {
      ...mapGetters(["isTwilioReady", "device", "checkDeviceStatus"]),
    },
    watch: {
      checkDeviceStatus(data) {
        if (data === "connect") {
          this.connectedCall = true;
          this.status = "connected";
        } else if (data == "disconnect") this.updateCallLog();
      },
    },
    mounted: async function() {
      this.visitor_select = "call_now";
      let response;
      try {
        response = await Call.getCapabilityVisitor(this.domain_id, this.flow_id, this.api_key);
        this.$store.dispatch("getToken", `${response[0].pref_value}?To=PBX_Guest_${Math.floor(Math.random() * 100000)}`);
        this.widgetData = JSON.parse(response[0].widgets);
        this.theme = this.widgetData.color == "#12263F" ? "visitor preview-keypad widget-dark" : "visitor preview-keypad";
      } catch {
        // eslint-disable-next-line
      }
    },
    methods: {
      // Get Callflow_id, domain_id, api_key and initiate call
      initiateCall() {
        this.connection = this.device.connect({ callflow_id: this.flow_id, domain_id: this.domain_id, api_key: this.api_key });
        this.$store.dispatch("twilioDeviceListeners");
        this.twilioConnection = this.connection;
      },
      // Send digits
      digitPressed(digits) {
        this.twilioConnection.sendDigits(digits.toString());
      },
      // Disconnect call
      disconnect_call() {
        this.device.disconnectAll();
        this.connectedCall = false;
        this.status = "";
      },
      // Visitor can choose 'call from computer' or 'call back' option to initiate call
      async showCallNow(data) {
        this.visitor_select = data;
        if (data == "call_back") {
          await this.disconnect_call();
        }
      },
      async prefer() {
        this.is_call_now = !this.is_call_now;
        this.visitor_select = "call_now";
        this.showText = false;
        if (this.is_call_now) {
          await this.initiateCall();
          this.buttonName = "Calling";
          this.variant = "danger";
          this.buttonName = "End Call";
        } else {
          await this.disconnect_call();
          this.buttonName = "Call Now";
          this.variant = "success";
        }
      },

      // Update call log on call disconnect
      async updateCallLog() {
        this.buttonName = "Call Now";
        this.variant = "success";
        this.connection = "";
        let form = {};
        form.callSid = this.twilioConnection.parameters.CallSid;
        form.flow_id = this.flow_id;
        form.api_key = this.api_key;
        form.domain_id = this.domain_id;
        CallLog.add(form);
      },
    },
  };
</script>
<style scoped>
  @import url("/css/pbxpluscss/keypadvisitor.css");
</style>
