<script>
  export default {
    props: {
      connection: Object,
    },
    data() {
      return {
        phone_number: "",
        is_visitor: this.$route.params.from == "visitor" ? true : false,
      };
    },
    methods: {
      sendDigit(number) {
        this.phone_number = this.phone_number + number;
        this.$emit("digitPressed", number);
      },
      sendNumber() {
        this.$emit("phoneNumber", this.phone_number);
      },
    },
  };
