import axios from "axios";
import JWT from "./jwt";
import Data from "../core/data";

// Server url to update call log info
const Call_Log = "https://api.${region}.500apps.com/pbxplus/v1/call-log?app_name=pbxplus";

class CallLog extends Data {
  // Method used to send call back info
  // Update end time in call logs
  async add(form) {
    let headers = await JWT.setHeader();
    headers["x-api-key"] = form.api_key;
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = Call_Log;
    url = eval("`" + url + "`");
    let response = await axios.put(url, form, { headers });
    return response.data;
  }
}
export default new CallLog();
